import io from 'socket.io-client';
import auth from '../store/modules/auth';
import logger from '../services/logger.service';
var isProduction = process.env.NODE_ENV === 'production';
var socketURL = isProduction
    ? 'https://server.grigoblin.ru'
    : 'http://maxbook.local:8001';
var socket = io(socketURL, { query: { token: auth.state.accessToken } });
function connect() {
    console.log('connect()');
    socket.on('connect', function () {
        logger.log("socket connected " + socket.id);
        var token = auth.state.accessToken;
        logger.log({ token: token });
        socket.emit('authorize', { token: token }, function (data) {
            console.log(data);
        });
    });
    socket.on('disconnect', function () {
        logger.log("socket disconnected");
    });
    socket.on('error', function (err) {
        logger.error(err.message);
    });
}
export default { connect: connect };



